.btn {
  display: block;
  margin: 0 auto;
}

.btn {
  background: #0ead92;
  background-image: -webkit-linear-gradient(top, #0ead92, #0ead92);
  background-image: -o-linear-gradient(top, #0ead92, #0ead92);
  background-image: -webkit-gradient(linear, left top, left bottom, from(#0ead92), to(#0ead92));
  background-image: linear-gradient(to bottom, #0ead92, #0ead92);
  border-radius: 28px;
  font-family: Arial;
  color: #ffffff;
  font-size: 20px;
  padding: 10px 20px 10px 20px;
  text-decoration: none;
  height: 10%;
  margin-top: 5px;
}

.btn:hover {
  background: #40cfb5;
  background-image: -webkit-linear-gradient(top, #40cfb5, #40cfb5);
  background-image: -o-linear-gradient(top, #40cfb5, #40cfb5);
  background-image: -webkit-gradient(linear, left top, left bottom, from(#40cfb5), to(#40cfb5));
  background-image: linear-gradient(to bottom, #40cfb5, #40cfb5);
  text-decoration: none;
}


